class Decoder {
    /**
     * @param libavModule
     * @param {function(output:Uint8Array,width:number,height:number):void}onSuccess
     * @param {function(errorCode:number):void}onError
     */
    constructor (libavModule, codecNumber, onSuccess, onError) {
      this._libavModule = libavModule
      /**
       * @type {function(Uint8Array, number, number): void}
       * @private
       */
      this._onSuccess = onSuccess
      /**
       * @type {function(): void}
       * @private
       */
      this._onError = onError
      /**
       * @type {number}
       * @private
       */
      this._inBuffer = this._libavModule._malloc(1024 * 1024)
      /**
       * @type {number}
       * @private
       */
      this._outBuffer = this._libavModule._malloc(3840 * 2160 * 4)
      /**
       * @type {number}
       * @private
       */
      this._outBufferSize = this._libavModule._malloc(4)
      /**
       * @type {number}
       * @private
       */
      this._outWidth = this._libavModule._malloc(4)
      /**
       * @type {number}
       * @private
       */
      this._outHeight = this._libavModule._malloc(4)
  
      this._libavModule._init_lib()
      this._codecContext = this._libavModule._create_codec_context(navigator.hardwareConcurrency, codecNumber)
    }
  
    release () {
      this._libavModule._destroy_codec_context(this._codecContext)
      this._libavModule._free(this._inBuffer)
      this._libavModule._free(this._outBuffer)
      this._libavModule._free(this._outBufferSize)
      this._libavModule._free(this._outWidth)
      this._libavModule._free(this._outHeight)
    }
  
    /**
     * @param {Uint8Array} nal
     */
    decode (nal) {
      this._libavModule.HEAPU8.set(nal, this._inBuffer)
      const ret = this._libavModule._decode(this._codecContext, this._inBuffer, nal.byteLength, this._outBuffer, this._outBufferSize, this._outWidth, this._outHeight)
      if (ret !== 0) {
        this._onError(ret)
        return
      }
      const width = this._libavModule.getValue(this._outWidth, 'i32')
      const height = this._libavModule.getValue(this._outHeight, 'i32')
      const outBufferSize = this._libavModule.getValue(this._outBufferSize, 'i32')
      const pic = new Uint8Array(this._libavModule.HEAPU8.subarray(this._outBuffer, this._outBuffer + outBufferSize).slice())
      this._onSuccess(pic, width, height)
    }
  }
  
  export default Decoder