import Decoder from './Decoder'
import DecoderModule from './libav'
const h264Decoders = {}
async function decoderWorkerInit() {
  self.Module = await DecoderModule();
  self.codec = 264;
  self.addEventListener('message', ({ data: { data, offset, renderStateId, codec, type } }) => {
    switch (type) {
      case 'decode': {
        let decoder = h264Decoders[renderStateId]
        if (self.codec != codec) {
          self.codec = codec
          if (decoder) {
            decoder.release()
            delete h264Decoders[renderStateId]
            decoder = null
          }
        }
        if (!decoder) {
          decoder = new Decoder(
            self.Module,
            self.codec,
            (output, width, height) => {
              postMessage({
                type: 'success',
                width,
                height,
                renderStateId,
                data: output.buffer
              }, [output.buffer])
            },
            errorCode => postMessage({ type: 'error', errorCode }))
          h264Decoders[renderStateId] = decoder
        } 
        const uint8Array = new Uint8Array(data).subarray(offset)
        decoder.decode(uint8Array)
        break
      }
      case 'release': {
        const decoder = h264Decoders[renderStateId]
        if (decoder) {
          decoder.release()
          delete h264Decoders[renderStateId]
        }
        break
      }
    }
  })

  self.postMessage({ 'type': 'ready' })
}

export default decoderWorkerInit;
